.icon-scroll{
    width: 20px;
    height: 30px;
    margin-left: -20px;
    margin-top: -35px;
    box-shadow: inset 0 0 0 2px black;
    border-radius: 25px;
    cursor: pointer;
    transition: box-shadow .6s;
}
.icon-scroll:hover{
    box-shadow:  inset 0 0 0 2px gray;

}
.icon-scroll:hover:before{
    background: gray;
}
.icon-scroll:before{
    content: '';
    width: 4px;
    height: 6px;
    background: black;
    margin-left: -2px;
    top: 8px;
    border-radius: 4px;
    animation-duration: 1.7s;
    animation-iteration-count: infinite;
    animation-name: scroll;
    opacity: 1;
    transition: background .6s;
}


@keyframes scroll{
    0% {
        opacity: 1;
        transform: translateY(-2px)
    }
    50%{
        transform: translateY(10px)
    }
    100%{
        transform: translateY(-2px)
    }
}

