}
.select{
  border: 1px solid #e7e8e9;
}
.option{
  //padding-left: 20px;
  cursor: pointer;
}
.selectPlaceholder{

  display: none;
}
.sendButton{
  font-family: Helvetica Now Display;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: 0.015em;
  margin-top: 6%;
  padding-left: 0;
  padding-right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 2px solid transparent;
  transition: border-bottom-color .3s;
  cursor: pointer;
}
.sendButton:hover{
  border-bottom: 2px solid #0F141E;
}
.sendText{
  margin-left: 10px;
}
.messageProgress,.messageEnd{
  font-family: Helvetica Now Display;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 26px;
  letter-spacing: 0.015em;
  margin-top: 6%;
  padding-left: 0;
  padding-right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  outline: none;
  border: none;
  display: none;
}
.loadSVG{
  transform-origin: 50% 50% 0;
  transition: all .1s;
  animation-name: rot;  /* задаём имя анимации (соответствует имени назначенному в ключевых кадрах)  */
  animation-duration: 10s;  /* задаём продолжительность анимации */
  animation-iteration-count: infinite;
  animation-delay: 0s;
}
@keyframes rot {
  0%   {transform: rotate(90deg);}
  //25%  {transform: rotate(180deg);}
  //75%  {transform: rotate(270deg);}
  100% {transform: rotate(2160deg);}
}