.menu{
  font-family: Helvetica Now Display;
  font-style: normal;
  font-weight: 800;
  font-size: 13px;
  line-height: 137.5%;
  letter-spacing: 0.2em;
  //color: #0F141E;
  transition: color .3s;
  margin-bottom: 4px;
}
.menuBar{
  margin-left: 13px;
}
.iron{
  width: 22px;
  height: 3px;
  background: black;
  margin-bottom: 4px;
}

.headerRightBar{
  $hrbHover:false !global;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.menuBar{
  margin-bottom: 2px;
}
.bubleClose{
  display: none;
  justify-content: center;
  //align-items: center;
}
.clz{
  margin-top: -2px;
  transition: all .3s;
}
.markX{
  fill:  #878A8F;
  transition: fill .3s;
}
.bubleClose:hover{
  .menu{
    color: #ffffff
  }
  .clz{
    transform: rotate(90deg);
  }
  .markX{
    fill: #ffffff
  }
}
.bubleMenu{
  display: flex;
  justify-content: center;
}
#i1,#i3{
  transition: transform .3s;
}
#i1,#i2,#i3{
  transition: background .3s;
}
#i4,#i5{
  display: none;
  z-index: 5;
}

.bubleMenu:hover{
  .menu{
    color:#878A8F;
  }
  #i1{
    transform: translateY(-3.5px);
    background: #878A8F;
  }
  #i2{
    background: #878A8F;
  }
  #i3{
    transform: translateY(3.5px);
    background: #878A8F;
  }
}
