.goContactBg{
  height: 46px;
  width: 46px;
  display: flex;
  //justify-content: center;
  border-radius: 40px;
  background: #878a8f;

  opacity: 0.6;
  z-index: -1;
  transition: all .2s cubic-bezier(.3,0,.5,1) .2s;
}
.goContactBlock{
  //width: max-content;
  //height: max-content;
  position: fixed;
  bottom:40px;
  cursor: pointer;
  left: 40px;
  //display: flex;
  //align-items: center;
}
.goContactBlock:hover{
  .goContactBg{
    width: 130px;
    opacity: 1;
    background: #0F141E;
  }
  .goContactText{
    display: inherit;
  }
}
#goContact{
  position: relative;
  left: 12.5px;
  top: 34.6px;
  font-size: 16px;
  z-index: 100;
  color: #ffffff
  //color: red;
}
.goContactText{
  transition: all .2s cubic-bezier(.3,0,.5,1) .2s;
  display: none;
  position: absolute;
  top: 33.5px;
  left: 43px;
  float: left;
  overflow: hidden;
  font-size: .8125rem;
  line-height: 1.25rem;
  font-weight: 500;
  color: #ffffff;
}
.goTooBlock {
  position: fixed;
  bottom: 40px;
  cursor: pointer;
  right: 40px;
  display: none;
  opacity: 0;
  transition: opacity ease-in-out .3s;
  //transition: all .9s cubic-bezier(.3,0,.5,1) .9s;
}
.goTooBlock:hover{
  .goTopBg{
    opacity: 1;
    background: #0F141E;
  }
}
.goTopBg{
  height: 46px;
  width: 46px;
  display: flex;
  border-radius: 40px;
  background: #878a8f;
  opacity: 0.6;
  z-index: -1;
  transition: all .1s ;
}
.goTop{
  position: relative;
  //left: 15.3px;
  left: 14.7px;
  //top: 36.5px;
  top: 38.3px;
  font-size: 16px;
  z-index: 100;
  color: #ffffff
}

.loader{
  position: absolute;
  overflow-y: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #0e1322;
  z-index: 1000;
  transform: translateY(100vh);
  transition: all .65s ;
}
.loaderOpen{
  transform: translateY(0);
}
.loaderClose{
  transform: translateY(-100vh);
  transition: all .65s ;
}