.project_1,.project_2,.project_3,.project_4{
  background: black;
  width: 670px;
  height: 838px;
  position: relative;
  cursor: pointer;
}
//.,.project_2{
//  width: 670px;
//  height: 838px;
//  position: relative;
//  cursor: pointer;
//}
.project_2,.project_4{
  margin-left: 60px;
}
@media (min-width: 1701px) and (max-width: 1920px) {

}
.projectBlock__textContent,.projectBlock__textContent__hover{
  margin-left: 60px;
  position: absolute;
  top: 0
}
.pbbgDark{
  width: 100%;
  height: 100%;
  background: #0F141E;
  opacity: 0.9;
  position: absolute;
  top:0;
  display: none;
  //transition: all .2s ease-out;
}

.projectBlock__year,.projectBlock__year_hover{
  font-family: Helvetica Now Display;
  font-style: normal;
  width: 20%;
  margin-right: 0;
  color: #878A8F;
  padding-top: 55px;
  padding-bottom: 5px;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: .4em;
  line-height: 1.25rem;
  height: max-content;
}
.projectBlock__title,.projectBlock__title_hover{
  font-family: Helvetica Now Display;
  font-style: normal;
  font-weight: 800;
  font-size: 33px;
  line-height: 40px;
  letter-spacing: -0.022em;
  color: #fff;
  width: 49.4%;
  margin-top: 5px;
}
.projectBlock{
  overflow:hidden;
  width: 100%;
  height: 100%;
  background-size: 100%;
}
.pb1bg{
  width: 100%;
  height: 100%;
  z-index: -50;
  transition: all 1s ease-out;
}
.projectBlock__textContent__hover{
  display: none;
}
