.project_1:hover,.project_2:hover{
  .pb1bg{
    transform: scale(1.1);
  }
  .projectBlock__textContent{
    display: none;
  }
  .projectBlock__textContent__hover{
    display: inherit;
  }
  .pbbgDark{
    display: inherit;
  }
}


