.ReactModalPortal{
  a.active{
    color: #ffffff;
  }
}

.ReactModal__Overlay {
  transform: translateY(100vh);
  transition: all .65s ;
}

.ReactModal__Overlay--after-open {
  transform: translateX(0);
  -webkit-overflow-scrolling:touch;
  html { overflow-y: hidden !important; }
  overflow-y: hidden;
}

.ReactModal__Overlay--before-close {
  transform: translateY(-100vh);
}

.mymodal{
  position:fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #0F141E;
   overflow: auto;
  -webkit-overflow-scrolling:touch;
  padding:0;
  outline:none;
  border: none;
}
.myoverlay{
    position: fixed;
    height: 100%;
    width:100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background:#0F141E;
    border: none;
    padding: 0;
}
