@font-face {
  font-family: 'Helvetica Now Display';
  font-weight: 400;
  src: local('Helvetica Now Display'), url(./fonts/HelveticaNowDisplay-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Helvetica Now Display';
  font-weight: 500;
  src: local('Helvetica Now Display'), url(./fonts/HelveticaNowDisplay-Regular.ttf) format('truetype');
}
@font-face {
  font-family: 'Helvetica Now Display';
  font-weight: 700;
  src: local('Helvetica Now Display'), url(./fonts/HelveticaNowDisplay-ExtraBold.ttf) format('truetype');
}
@font-face {
  font-family: 'Helvetica Now Display';
  font-weight: 800;
  src: local('Helvetica Now Display'), url(./fonts/HelveticaNowDisplay-ExtraBold.ttf) format('truetype');
}
.Header{
  /*padding-top: 2.1050%;*/
  z-index: 10;

}
.wrapper{
  z-index: -10;
}
.header__content{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 47px;
}
path.logoName{
  transition: fill .3s;
}
.headerLeftBar{
  cursor: pointer;
  position: fixed;
  z-index: 100;
}
.headerRightBar{
  margin-right: 1px;
  position: fixed;
  z-index: 100;
}
.bg2{
  position: absolute;
  display: block;
  width: 95%;
  height: auto;
  /*top: calc(50px + 2%);*/
  z-index: -10;
  -webkit-filter: drop-shadow(18px 18px 16px #f7f7f7);
  filter: drop-shadow(18px 18px 30px #f7f7f7);
}
.App{
  z-index: 20;
  overflowY: scroll;
}
.Goo{
  z-index: -10;
}
.shortLinks{
  display: flex;
  align-items: flex-end;
}
.viewProjects ,.readAboutMe{
  font-family: Helvetica Now Display;
  color: #0f141e;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 100%;
  letter-spacing: normal;
  padding-bottom: 3px;
  border-bottom: solid transparent 2px;
  transition: border-bottom-color .3s;
}
.viewProjects:hover,.readAboutMe:hover{
  cursor: pointer;
  border-bottom: solid #0f141e 2px;

}
.or{
  font-family: Helvetica Now Display;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  letter-spacing: -0.01em;
  color: #878A8F;
  padding-bottom: 3px;
  border-bottom: solid transparent 2px;
}
a{
  text-decoration: none;
}
.bg1{
  position: absolute;
  top: 9%;
  opacity: 1;
  /*left: 49%;*/
  right: 4.7%;
  z-index: -10;
  transition: opacity .25s ease-in;

}
.Tag{
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 102.5%;
  padding-bottom: 5px;
  padding-top: 5px;
  letter-spacing: 0.405em;
  width: 110%;
  color: #878A8F;
}
.Post{
  padding-top: 5px;
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: 800;
  line-height: 2.5rem;
  letter-spacing: -0.01em;
  color: #0F141E;
}
.nameInfo{
  height: max-content;
  padding-top: 2px;
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: normal;
  line-height: 1.5625rem;
  color: #878A8F;
}
.footMP{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 6.8%;
  /*opacity: 0;*/
  transition: opacity 1s;
}
.icon-scroll,
.icon-scroll:before{
  left: 51%;
  position: absolute;
}
#textBlock{
  /*margin-top: 23.47%;*/
  z-index: 2;
}
#t2{
  padding-top: 6.7%;
  margin-top: 8.3%;
}

.blockHrefContact{
  margin-top: 7.7%;
  width: 100%;
  display: block;
  align-items: center;
  justify-content: center;
}
.arrow{
  margin-top: 18px;
  margin-left: 20px;
}
.hrefContact{
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  padding-bottom: 5px;
  border-bottom: 6px solid  transparent;
  transition: border-bottom-color .3s;
}
.hrefContact:hover{
  border-bottom: 6px solid  #0F141E;
  cursor: pointer;
}

.tagBot{font-family: 'Helvetica Now Display';
  font-style: normal;
  font-size: 14px;
  padding-bottom: 5px;
  padding-top: 5px;
  width: max-content;
  color: #878A8F;
  font-weight: 400;
  letter-spacing: .4em;
  line-height: 1.25rem;
  height: max-content;
}
.tagBotBlock, .blockArrowHref{
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer{
  max-width: 1400px;
  min-width: 1000px;
  margin-top: 7.7%;
 }
.footer_content{
  display: flex;
  align-items: start;
  justify-content: space-between;
  padding-top: 74px;
  border-top: 1px solid #e7e8e9;
  width: 100%;
}
.corpInfo{
  width: 100%;
  height: 60px;
  margin-top: 40px;
  padding-bottom: 95px;
  color: #878A8F;
  display: flex;
  align-items: center;
}
.corp{
  /*width: 215px;*/
  margin-left: 10px;
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 157%;
  /* or 20px */
  white-space: pre-line;
  letter-spacing: 0.03em;
}
#love{
  color: #ced2d9;
}
.footerBar_1{
  width: max-content;
}
.projects_34{
  padding-bottom: 60px;
}
.fbName{
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: 800;
  font-size: 22px;
  line-height: 136%;
  width: 150px;
  height: max-content;
  letter-spacing: 0.005em;
  color: #0F141E;
  padding-top: 2px;
  padding-bottom: 3px;
}
.fbNameLow{
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: 800;
  font-size: 22px;
  line-height: 136%;
  width: 100px;
  height: max-content;
  letter-spacing: 0.005em;
  color: #0F141E;
  padding-top: 2px;
  padding-bottom: 3px;
}
.fb1Text{
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 158%;
  letter-spacing: 0.01em;
  /* or 25px */
  margin-right: 0px;
  width: 400px;
  color: #878A8F;
  padding-top: 2px;
  padding-bottom: 3px;
  margin-top: 10px;
}

.fb3Text{
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 158%;
  letter-spacing: 0.01em;
  /* or 25px */
  width: 200px;
  color: #878A8F;
  padding-top: 2px;
  padding-bottom: 3px;
  margin-top: 10px;
}
.fbHrefProject{
  width: max-content;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}


.borderBotHref{
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 158%;
  color: #0F141E;
  border-bottom: 0.8px solid  transparent;
  transition: border-bottom-color .3s;
  height: 17px;
  /*vertical-align: top;*/
  padding-bottom: 5px;
}
.borderBotHref:hover {
  border-bottom: 0.8px solid #0F141E;
  cursor: pointer;
}
#fbHrefProjectMargin{
  margin-top: 10px;
  padding-top: 2px;
}
.fb1Contacts, .mail, a1{
  text-decoration:none;
  font-family: 'Helvetica Now Display';
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 158%;
  /* or 25px */
  width: 220px;
  letter-spacing: 0.01em;
  color: #878A8F;
  padding-top: 2px;
  padding-bottom: 3px;
  margin-top: 10px;
}

a1{
  color: #0F141E;
  cursor:text;
  -webkit-user-select: text;
  -moz-user-select: text;
  user-select: text;
}
::selection {
   background: #86898F; /* Цвет фона */
   color: #fff; /* Цвет текста */
 }
.mail{
  border-bottom: 0.8px solid  transparent;
  transition: border-bottom-color .3s;
  color: #0F141E;
  -webkit-user-select: text;
  -moz-user-select: text;
  user-select: text;
}
.mail:hover{
  border-bottom: 1px solid #0F141E;
  border-bottom: 0.8px solid #0F141E;
}

.ReactModal__Body--open {
  overflow: hidden;
}

