.projectWrapper{
    margin-left: 211px;
    margin-right: 211px;
    margin-top: 111px;
    display: block;
}
.projectText{
    display: flex;
    justify-content: space-between;
}
.rightBar{
    width: 50%;
    margin-top: 28px;
}
.leftBar{
    width: 30%;
}
.projectTag{
    font-family: Helvetica Now Display;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 102.5%;
    padding-bottom: 5px;
    padding-top: 5px;
    letter-spacing: 0.405em;
    color: #878A8F;
}
.projectPost{
    font-family: Helvetica Now Display;
    font-style: normal;
    font-weight: 800;
    font-size: 44px;
    line-height: 3.125rem;
    /* or 50px */
    margin-top: 5px;
    padding-top: 4px;
    padding-bottom: 1px;
    letter-spacing: -0.01em;
    color: #0F141E;
    width: 98%;
    height: max-content;
}
.projectMiniTextBlock{
    margin-top: 30px;
    width: 50%;
}
.projectDate{
    font-family: Helvetica Now Display;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    /*line-height: 3.125rem;*/
    /* or 50px */
    padding-top: 1px;
    padding-bottom: 4px;
    letter-spacing: -0.01em;
    color: #0F141E;
    /*width: 25%;*/
    height: max-content;
}
.projectParagraph{
    height: max-content;
    padding-top: 2px;
    padding-bottom: 3px;
    margin-top: 25px;
    font-family: Helvetica Now Display;
//font-style: normal;
    color: #878A8F;
    font-size: 22px;
    letter-spacing: normal;
    line-height: 1.875rem;
    font-weight: 500;
}
.projectPerson, .projectNext{
    height: max-content;
    padding-top: 1px;
    padding-bottom: 4px;
    margin-top: 20px;
    font-family: Helvetica Now Display;
//font-style: normal;
    color: #878A8F;
    font-size: 18px;
    letter-spacing: normal;
    line-height: 1.875rem;
    font-weight: 500;
    display: flex;
}
.projectNext{
    justify-content: center;
    margin-top: 10px;
}
.a1{
    color: #0F141E;
    cursor:text;
    -webkit-user-select: text;
    -moz-user-select: text;
    user-select: text;
    margin-left: 7px;
    margin-right: 7px;
}
.projectImages{
 padding-top: 130px;
}
.projectImageBig{
    width: 100%;
    height: 1075px;
    margin-bottom: 60px;
}
.projectImageSG{
    margin-bottom: 60px;
    width: 100%;
    height: 520px;
}
.projectImageMedium{
    width: 100%;
    height: 825px;
    margin-bottom: 60px;
}
.projectImageDouble{
    width: 100%;
    height: max-content;
    margin-bottom: 60px;
    display: flex;
    justify-content: space-between;
}
.projectImageMin{
    width: 670px;
    height: 670px;
}
.spaceNextProject{
    margin-top: 1300px;
    margin-bottom: 600px;
}
.outsite{
    cursor: pointer;
    border-bottom: 1px solid transparent;
    transition: all ease-in-out .25s;
}
.outsite:hover{
    border-bottom: 1px solid #0F141E;
}
.projectSayScroll{
    font-family: Helvetica Now Display;
    font-style: normal;
    font-weight: 800;
    font-size: 44px;
    line-height: 3.125rem;
    /* or 50px */
    margin-top: 130px;
    padding-top: 4px;
    margin-left: 80px;
    padding-bottom: 1px;
    letter-spacing: -0.01em;
    color: #0F141E;
    width: 90%;
    height: max-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.tak{
    width: 1px;
    height: 1px;
    background: transparent;
}
.next{
    display: none;
    position: fixed;
    opacity: 0;
    /*bottom: 200px;*/
    margin-bottom: 40px;
    padding-bottom: 400px;
    top: 10%;
    left: 0;
    transition: opacity ease-in-out .2s;
}
.party{
    display: block;
}
.person{
    display: flex;
}